<template>
<div id="recordPage">
    <div class="title">监控记录</div>
    <div class="record-content">
        <div class="search-form">
            <el-form ref="searchform" :model="searchform" label-width="70px"  :inline="true">
                <el-form-item label="事件：" label-width="42px">
                    <el-select v-model="searchform.event" 
                            size="small"
                            clearable
                            placeholder="请选择事件"
                            :style="inputStyle2">
                        <el-option
                        v-for="(item,index) in allEventArr"
                        :key="index"
                        :label="item"
                        :value="item">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="日期：" label-width="55px">
                    <el-date-picker
                        size="small"
                        v-model="searchform.dateTime"
                        type="daterange"
                        align="right"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        @change="consolechange"
                        value-format='yyyy-MM-dd'
                        :style="inputStyle"
                        >
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="位置：" label-width="55px">
                    <el-input 
                    size="small" 
                    placeholder="请输入设备" 
                    v-model="searchform.device" 
                    clearable 
                    :style="inputStyle2"/>
                </el-form-item>
                <el-form-item label="识别结果："  label-width="80px">
                    <el-select v-model="searchform.result   " 
                            clearable
                            size="small"
                            placeholder="请选择"
                            :style="inputStyle2">
                        <el-option
                        v-for="(item,index) in allVaildResultArr"
                        :key="index"
                        :label="item"
                        :value="item">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="人员类型：" label-width="80px">
                    <el-select v-model="searchform.personType   " 
                            clearable
                            size="small"
                            placeholder="请选择"
                            :style="inputStyle2">
                        <el-option
                        v-for="(item,index) in allPersonTypeArr"
                        :key="index"
                        :label="item"
                        :value="item">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item >
                    <el-input size="small" 
                        style="margin-left: 10px"
                        placeholder="请输入姓名" 
                        v-model="searchform.name" 
                        clearable 
                        :style="inputStyle2"/>
                </el-form-item>
                <el-form-item>
                    <el-button
                        size="small"
                        type="primary"
                        class="btn"
                        @click="searchtabledata"
                    >
                    查询
                    </el-button>
                </el-form-item>
            </el-form>
            <div class="fix">
                <div class="export"></div>
                <div class="del"></div>
            </div>
        </div>
    
        <!-- <div class="batchbox">
            <el-button class="btn" @click="batchremove" :disabled="batchdisabled">批量删除</el-button>
            <el-button class="btn" @click="batchexport" :disabled="batchdisabled">批量导出</el-button>
        </div> -->
        <div class="line"></div>
        <div class="table-box" ref="tableBox">
            <el-table
                ref="table"
                :data="tableData"
                @selection-change="handleSelectionChange"
                style="width: 100%"
                :row-key="getRowKeys"
                >
                <el-table-column
                    fixed
                    type="selection"
                    width="55"
                    reserve-selection
                ></el-table-column>
                <el-table-column
                    v-for="(item,index) in tableDataLabel"
                    :key="index"
                    :label="item"
                    :prop="item"
                    align="center"
                >
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <div class="pagination">
                <el-pagination
                    ref="pagination"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5,10,20]"
                    :page-size.sync="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</div>
</template>
<script>
export default {
  data() {
    return {
      batchdisabled: true,
      allEventArr: [],
      allVaildResultArr: [],
      allPersonTypeArr: [],
      tableData: [],
      tableDataLabel: ['序号', '姓名', '人员类型', '位置', '识别结果', '对比打分', '事件说明', '照片', '时间', '操作'],
      currentPage: 1,
      pageSize: 10,
      total: 0,
      searchform: {
        event: '',
        dateTime: '',
        device: '',
        result: '',
        personType: '',
        name: '',
        netApi: ''
      },
      getRowKeys(row) {
        return row.id
      },
      inputStyle: {
        width: '220px',
        height: '34px'
      },
      inputStyle2: {
        width: '150px',
        height: '34px'
      }
    }
  },
  created() {
    this.netApi = this.$api().get('minitor_record');
    console.log(this.netApi)
    this.getTableList()
  },
  mounted() {
    //自动改变teble高度
    let tableHeight = this.$refs.tableBox.clientHeight;
    this.$nextTick(() => {
    //   this.$refs.table.$el.style.height = tableHeight - 10 + "px";
      this.$refs.table.$el.style.height = tableHeight - this.$refs.pagination.$el.style.height + "px";
    });
  },
  methods: {
    getTableList() {
      this.$http.get(this.netApi.query, {
        params: {type: '1234567890'}
      }).then(res => {
        console.log(res)
      })
    },
    searchtabledata() {

    },
    consolechange() {
      console.log(this.dateTime);
    },
    // 批量删除
    batchremove() {

    },
    // 批量导出
    batchexport() {

    },
    handleSelectionChange(val) {
      if(val.length > 0) {
        this.batchdisabled = false
      }else{
        this.batchdisabled = true
      }
      this.multipleSelection = val;
    },
    handleSizeChange (pageSize) {
      this.pageSize = pageSize;
      this.searchtabledata();
    },
    handleCurrentChange (Current) {
      this.currentPage = Current;
      this.searchtabledata();
    },
  }
}
</script>

<style lang="scss">
#recordPage{
    position: absolute;
    top: 0;
    left: 30px;
    right: 30px;
    bottom: 20px;
    background: #fff;
    .record-content {
        padding: 0 30px;
    }
    .el-form-item__label {
        padding: 0 !important;
    }
    .el-table th, .el-table tr {
      background-color: #F5F8FC !important;
      color:rgba(37,44,53,1);
    }
    .title{
        background: #F5F8FC;
        height: 80px;
        line-height: 80px;
        font-size: 20px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
    }
    .search-form{
        position: relative;
        display: flex;
        height: 1.5rem;
        align-items: center;
        padding-top: 24px;
        .fix {
            position: absolute;
            right: 0;
            top: 42px;
            .export {
                display: inline-block;
                background: url('../../cp_img/export.png') no-repeat;
                &:hover {
                background: url('../../cp_img/export_act.png') no-repeat !important;
                }
                width: 22px;
                height: 22px;
                margin-right: 20px;
            }
            .del {
                display: inline-block;
                background: url('../../cp_img/deletion.png') no-repeat;
                &:hover {
                background: url('../../cp_img/deletion_act.png') no-repeat !important;
                }
                width: 22px;
                height: 22px;
            }
        }
    }
    .batchbox {
        box-sizing: border-box;
        text-align: right;
        padding-right: 1rem;
        margin-top: -0.2rem;
    }
    .table-box{
        >.pagination{
            text-align: center;
        }
    }
}
</style>


